<template>
  <div class="SoftwareWorkTotalBackground">
    <!--     <input
      type="text"
      class="SoftwareWorkUserNumberInput"
      v-model="SoftwareWorkUserNumber"
      placeholder="请输入学生学号"
    />
    <button  class="SoftwareWorkSeekButton" @click="SoftwareWorkSeekButtonClick">查询</button>
    <button  class="SoftwareWorkEditButton" @click="SoftwareWorkEditButtonClick">编辑</button>
 -->
    <div class="SoftwareWorkTableBackground">
      <table>
        <thead>
          <tr>
            <th style="width: 2%">学号</th>
            <th style="width: 2%">姓名</th>
            <th style="width: 6%">软件名称</th>
            <th style="width: 2%">完成日期</th>
            <th style="width: 2%">权力获取方式</th>
            <th style="width: 2%">权力范围</th>
            <th style="width: 2%">著作权登记号</th>
            <th style="width: 1%">颁发部门</th>
            <th style="width: 3%">发证日期</th>
            <th style="width: 1%">详情</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="records in StudentSoftwareWorkTable"
            :key="records.studentNumber"
          >
            <!-- 如果在tr标签里写点击事件就是一行  @click="handleRowClick(records.id)" -->
            <td>{{ records.softwareCopyrights[0].studentNumber }}</td>
            <td>{{ records.softwareCopyrights[0].studentName }}</td>
            <td>{{ records.softwareCopyrights[0].softwareName }}</td>
            <td>{{ records.softwareCopyrights[0].completionDate }}</td>
            <td>{{ records.softwareCopyrights[0].howToObtainRights }}</td>
            <td>{{ records.softwareCopyrights[0].scopeOfRights }}</td>
            <td>{{ records.softwareCopyrights[0].registrationNumber }}</td>
            <td>{{ records.softwareCopyrights[0].issuingDepartment }}</td>
            <td>{{ records.softwareCopyrights[0].issueDate }}</td>
            <td @click="ViewDetails(records)" class="Details">查看</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div class="SoftwareWorkPagination">
      <el-pagination
        size="small"
        :page-size="pageSize"
        :pager-count="10"
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="handlePageChange"
      />
    </div>
    <div v-if="ifDetails" class="SoftwareWorkDetailsBackground">
      <div class="SoftwareWorkDetailsFormBackground">
        <span class="SoftwareWorkDetailsFormTitle">软著详细信息</span>
        <form class="SoftwareWorkDetailsForm">
          <div class="label-row">
            <label>姓名 : {{ studentDetails[0].studentName }}</label>
            <label>学号 : {{ studentDetails[0].studentNumber }}</label>
          </div>
          <div v-for="(data, index) in studentDetails" :key="index">
            <div class="label-row">
              <label>软件名称 : {{ data.softwareName }}</label>
            </div>
            <div class="label-row">
              <label>软件完成日期 : {{ data.completionDate }}</label>
            </div>
            <div class="label-row">
              <label>权力获取方式 : {{ data.howToObtainRights }}</label>
            </div>
            <div class="label-row">
              <label>权力范围 : {{ data.scopeOfRights }}</label>
            </div>
            <div class="label-row">
              <label>软件著作权登记号 : {{ data.registrationNumber }}</label>
            </div>
            <div class="label-row">
              <label>颁发部门 : {{ data.issuingDepartment }}</label>
            </div>
            <div class="label-row">
              <label>发证日期 : {{ data.issueDate }}</label>
            </div>
            <br />
          </div>
        </form>
        <button
          class="SoftwareWorkDetailsFormCloseButton"
          @click="SoftwareWorkDetailsFormCloseButtonClick"
        >
          关闭
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "@/axios";

export default {
  data() {
    return {
      StudentSoftwareWorkTable: [],
      ifDetails: false,
      studentDetails: {},
      currentPage: 1,
      pageSize: 12,
      total: 130,
    };
  },
  mounted() {
    this.FetchData();
  },
  methods: {
    handlePageChange(page) {
      this.currentPage = page;
      this.FetchData();
    },

    async FetchData() {
      try {
        const informationResponse = await axios.get(
          "/admin/getAcademicPerformance/getSoftwareCopyright",
          {
            params: {
              page: this.currentPage,
              pageSize: this.pageSize,
            },
          }
        );
        if (informationResponse.code == 1) {
          this.total = informationResponse.data.total;
          this.StudentSoftwareWorkTable = informationResponse.data.records;
        } else {
          this.$message("数据加载失败!");
        }
      } catch (error) {
        this.$message("数据加载失败!");
        console.error("报错原因：", error);
      }
    },
    ViewDetails(records) {
      this.RenewLabel(records);
      this.ifDetails = true;
    },
    RenewLabel(response) {
      this.studentDetails = response.softwareCopyrights;
    },
    SoftwareWorkDetailsFormCloseButtonClick() {
      this.ifDetails = false;
    },
  },
};
</script>

<style scoped>
.Details {
  cursor: pointer;
  text-decoration: underline;
}
.Details:hover {
  color: #8491cd; /* 可选：当鼠标悬停时改变颜色 */
}
.SoftwareWorkPagination {
  position: absolute;
  top: 95%;
}
.SoftwareWorkUserNumberInput {
  /* position: absolute; */
  border-radius: 5px;
  outline: none; /* 外边线颜色不要 */
  border: none; /* 边框颜色不要 */
}

.SoftwareWorkTotalBackground {
  position: relative;
  width: 100%;
  height: 100%;
}
.SoftwareWorkTableBackground {
  position: absolute;
  width: 100%;
  height: 90%;
  overflow: auto;
}

@import "/src/components/ManageSystem/TableStyle.css";
@import "/src/components/ManageSystem/DetailsForm/SoftwareWorkForm.css";
@import "/src/components/ManageSystem/MouseScrollWheelStyle/DetailFormMouse.css";
</style>
